<template>
  <v-app-bar density="compact" absolute>
    <v-app-bar-nav-icon></v-app-bar-nav-icon>
    <v-app-bar-title class="pointer" @click="toHome">
      <div class="app-bar-icon-custom">
        <img src="/fishicon.svg" />{{ barInfo.title }}
      </div>
    </v-app-bar-title>

    <template v-if="showSearch">
      <!-- <v-divider inset vertical></v-divider> -->
      <SearchBar :inNav="true" />
      <!-- <v-divider inset vertical></v-divider> -->
    </template>
    <v-spacer v-else></v-spacer>

    <v-btn class="mr-3" text>{{ barInfo.language }}</v-btn>

    <v-divider inset vertical></v-divider>

    <template v-if="hasLogin.state === false">
      <v-btn class="mr-1 ml-3" color="primary" plain @click="toSignup">
        <v-icon left icon="mdi-account-plus"></v-icon>
        <span>{{ barInfo.signup }}</span>
      </v-btn>
      <v-btn class="mr-3 ml-1" color="primary" plain @click="toggleLogin(1)">
        <v-icon left icon="mdi-account-settings"></v-icon>
        <span>{{ barInfo.login }}</span>
      </v-btn>
    </template>
    <template v-else>
      <v-btn class="mr-1 ml-3" color="primary" plain>
        <v-icon left icon="mdi-cart"></v-icon>
        <span>{{ barInfo.cart }}</span>
      </v-btn>
      <v-btn class="mr-3 ml-1" color="primary" plain>
        <v-icon left icon="mdi-form-select"></v-icon>
        <span>{{ barInfo.order }}</span>
      </v-btn>
      <v-avatar
        ><img
          class="avatar"
          :alt="hasLogin.info.name"
          :src="hasLogin.info.avatar"
      /></v-avatar>
    </template>
  </v-app-bar>
  <div class="my-login-outer" v-if="showLogin" @click.self="toggleLogin(0)">
    <Login :exit="toggleLogin" />
  </div>
</template>

<script>
import { reactive, ref, watch, computed, onBeforeMount } from "vue";
import { useStore } from "vuex";
import SearchBar from "./BigSearch.vue";
import Login from "../widgets/login.vue";
export default {
  name: "NavBar",
  components: { SearchBar, Login },
  props: ["showSearch"],
  setup() {
    const store = useStore();
    const barInfo = reactive({
      title: "摸渔",
      language: "语言",
      login: "登录",
      signup: "注册",
      cart: "购物车",
      order: "我的订单",
    });

    let hasLogin = reactive({
      state: false,
      info: {},
    });
    let storelogin = computed(() => store.getters.LoginInfo);
    function storeLoginInfo(e) {
      if (e === false) {
        hasLogin.state = false;
        hasLogin.info = {};
      } else {
        hasLogin.state = true;
        hasLogin.info = {
          name: e.uname,
          avatar: e.avatar,
        };
      }
    }
    watch(
      () => storelogin,
      (e) => {
        storeLoginInfo(e);
      }
    );
    onBeforeMount(() => {
      storeLoginInfo(store.getters.LoginInfo);
    });

    let showLogin = ref(false);

    function toHome() {
      window.location.href = "/";
    }
    function toSignup() {
      window.location.href = "/register";
    }
    function toggleLogin(s) {
      if (s === 0 || s === undefined || s === null) showLogin.value = false;
      else showLogin.value = true;
    }

    return {
      barInfo,
      toHome,
      showLogin,
      toggleLogin,
      toSignup,
      hasLogin,
    };
  },
};
</script>

<style scoped>
.pointer {
  cursor: pointer;
  flex-direction: row;
}
.app-bar-icon-custom {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  align-items: center;
}
.app-bar-icon-custom img {
  width: 30px;
  margin-right: 0.2em;
}
* {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
}
/* .enable-select {
  user-select: auto;
} */
.my-login-outer {
  position: fixed;
  left: 0;
  top: 0;
  background-color: #00000066;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  z-index: 100;
}

.avatar {
  cursor: pointer;
}
</style>
