<template>
  <NavBar :showSearch="true" />
  <v-navigation-drawer permanent></v-navigation-drawer>
  <v-main>
    <v-card class="rec-card">
      <div class="rec-outer">
        <div class="rec-img-div">{{ rec.logo }}</div>
        <v-divider class="mr-2 ml-2" vertical></v-divider>
        <div class="rec-top-des">
          <span>{{ rec.title }}</span>
          <v-btn
            flat
            :ripple="false"
            width="fit-content"
            density="compact"
            class="rec-link"
            color="transparent"
            text-color="cyan lighten-2"
            >{{ rec.subtitle }}</v-btn
          >
        </div>
      </div>
      <div class="rec-outer-detail">
        <div class="rec-top-pic">{{ rec.pic }}</div>
        <div class="rec-plist">
          <a
            v-for="(item, idx) in rec.list"
            :href="item.href"
            :key="'reclist_' + idx"
          >
            <div class="rec-plist-per-pic">{{ item.pic }}</div>
            <span class="rec-plist-per-title">{{ item.title }}</span>
            <div class="rec-plist-per-rating-outer">
              <v-rating
                class="rec-plist-per-rating"
                v-model="item.rating"
                readonly
                size="x-small"
                color="yellow-accent-4"
                half-increments
                density="compact"
              ></v-rating>
              <span class="rec-plist-per-total">{{ item.total }}</span>
            </div>
          </a>
        </div>
      </div>
    </v-card>
    <div id="main-product">
      <div class="no-product" v-if="status !== 1">
        <v-progress-circular
          v-if="status === 0"
          :size="100"
          color="primary"
          indeterminate
        ></v-progress-circular>
        <div v-else-if="status === 2"></div>
        <div v-else-if="status === 3"></div>
        <div v-else-if="status === 4"></div>
        <span class="no-product-word">{{ lWords[status] }}</span>
      </div>
      <div v-if="status === 1" class="all-products" ref="prods">
        <v-card
          elevation="3"
          width="220"
          class="per-product"
          v-for="item in pList"
          :key="item.pid"
        >
          <div class="product-cover" @click="toHref(0, item.pid)">
            <v-img :src="item.cover"></v-img>
          </div>
          <div class="product-beyound-pic">
            <div class="product-per-line">
              <div class="product-price">{{ item.price }}</div>
              <v-rating
                v-if="item.rating > 10"
                class="rec-plist-per-rating"
                v-model="item.rating"
                readonly
                size="x-small"
                color="yellow-accent-4"
                half-increments
                density="compact"
              ></v-rating>
            </div>
            <div class="product-title" @click="toHref(0, item.pid)">
              {{ item.pname }}
            </div>
            <v-btn
              @click="toHref(1, item.store.sid)"
              block
              text-color="blue"
              class="pl-0 pr-0 flex-start my-cus-btn-style"
              :ripple="false"
              flat
              density="compact"
              prependIcon="mdi-store"
              >{{ item.store.name }}</v-btn
            >
          </div>
        </v-card>
        <v-pagination
          class="paging"
          v-model="page"
          :length="page_num"
          @click="goToSection"
        ></v-pagination>
      </div>
    </div>
  </v-main>
</template>

<script>
import {
  onBeforeMount,
  provide,
  ref,
  reactive,
  onUpdated,
  computed,
  watch,
} from "vue";
import { useStore } from "vuex";
import NavBar from "../components/NavBar.vue";
import axios from "axios";
export default {
  name: "searchPage",
  components: { NavBar },
  setup() {
    const perPage = 28;
    const prods = ref(null);
    let page = ref(1);
    let item_num = ref(0);
    let page_num = computed(() => Math.ceil(item_num.value / perPage));
    let kw = ref("");
    let status = ref(0);
    let ajaxResultList = reactive([]);
    let pList = reactive([]);
    /* 0: loading
       1: found
       2: not found
       3: error on server
       4: network error */
    const rec = reactive({
      logo: "推荐·商标占位符",
      title: "标题，躺平，不想了，广而告之",
      subtitle: "次标题，任尔摸，水中无鱼，心中有渔",
      pic: "热销广告图",
      list: [
        {
          pic: "picture_0",
          title: "这好像是个商品名字",
          href: "#",
          rating: 4.2,
          total: 22349,
        },
        {
          pic: "picture_1",
          title: "这好像也是个商品名字",
          href: "#",
          rating: 3.9,
          total: 4252,
        },
        {
          pic: "picture_2",
          title: "我不妨把商品名字弄得老长老长的测试一下看看折叠效果",
          href: "#",
          rating: 4.1,
          total: 1231,
        },
      ],
    });
    const lWords = reactive([
      "加载中",
      "",
      "没有找到相关结果，换个关键词试试",
      "服务器出错",
      "好像网络不太行",
    ]);
    const store = useStore();
    function hasResult(pg) {
      if (pg !== undefined && pg !== null && pg > 0) {
        page.value = pg;
      }
      //   console.log(res);
      let tmp = [];
      for (
        let i = (page.value - 1) * perPage;
        i < Math.min(page.value * perPage, item_num.value);
        i++
      ) {
        tmp.push(
          new Object({
            pid: ajaxResultList[i].pid,
            pname: ajaxResultList[i].pname,
            price: ajaxResultList[i].prices[0],
            cover: store.getters.Media(ajaxResultList[i].cover),
            rating: Math.round(Math.random() * 30) / 10 + 2,
            volumn: ajaxResultList[i].volumn,
            tag: ajaxResultList[i].tag,
            store: {
              sid: ajaxResultList[i].sid,
              name: ajaxResultList[i].sname,
              region: ajaxResultList[i].shipping_region,
            },
          })
        );
      }
      Object.assign(pList, tmp);
      // console.log(pList);
    }
    watch(page, (e) => {
      hasResult(e);
    });
    function search() {
      const s_kw = new URL(window.location.href).searchParams.get("kw");
      if (s_kw !== "" && s_kw !== null && s_kw !== undefined) {
        kw.value = s_kw;
        provide("sval", kw.value);
        axios
          .get(store.getters.UrlSearchTitle(kw.value))
          .then((res) => {
            if (res.status === 200) {
              // success
              if (res.data.code === 200) {
                if (res.data.data.item_num > 0) {
                  status.value = 1;
                  try {
                    page.value = 1;
                    item_num.value = res.data.data.item_num;
                    Object.assign(ajaxResultList, res.data.data.items);
                    hasResult(1);
                  } catch (e) {
                    console.error(e);
                  }
                } else {
                  status.value = 2;
                  alert("no result... try another keyword");
                }
              } else {
                status.value = 3;
                alert("something went wrong on the server");
              }
            } else {
              // maybe network error
              status.value = 4;
              alert("something went wrong on sending data");
            }
          })
          .catch((e) => console.error(e));
      } else {
        window.location.href = "/";
      }
    }
    onBeforeMount(() => {
      search();
    });
    onUpdated(() => {
      search();
    });
    function goToSection() {
      prods.value.scrollIntoView();
    }
    function toHref(ty, ar) {
      let tref = "/product?pid=" + ar;
      if (ty === 1) tref = "/store?sid=" + ar;
      if (store.state.ifPC) {
        window.open(tref);
      } else {
        window.location.href = tref;
      }
    }

    return {
      kw,
      rec,
      status,
      pList,
      lWords,
      hasResult,
      item_num,
      page,
      page_num,
      goToSection,
      prods,
      toHref,
    };
  },
};
</script>

<style scoped>
.rec-card {
  margin: 1em;
  padding: 1em 0.8em 0.8em 0.8em;
}
.rec-outer {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
}
.rec-img-div {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: center;
  background-color: #01579b;
  color: white;
  cursor: pointer;
  width: 200px;
  height: 60px;
  border-radius: 10px;
  box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%),
    0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
}
.rec-top-des {
  display: flex;
  flex-direction: column;
}
.rec-link {
  padding: 0;
  text-decoration: underline;
}
.rec-top-des span:first-child {
  cursor: default;
  font-size: 1.6em;
}
.rec-outer-detail {
  display: flex;
  flex-direction: row;
  margin: 1em 0;
  height: 8em;
  background-color: #e1f5fe;
  position: relative;
  overflow: hidden;
  box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%),
    0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
}
.rec-top-pic {
  --blrw: 30px;
  width: calc(300px - 2 * var(--blrw));
  height: calc(100% - 2 * var(--blrw));
  background-color: #4fc3f7;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  justify-content: center;
  margin: var(--blrw);
  box-shadow: 0 0 30px var(--blrw) #4fc3f7;
}
.rec-plist {
  display: flex;
  flex-direction: row;
  margin-left: 0.6em;
}
.rec-plist > a {
  text-decoration: none;
  color: black;
  list-style: none;
  width: 10.5em;
  margin: 0.3em;
  background-color: white;
  box-shadow: 0px 2px 4px -1px rgb(0 0 0 / 20%),
    0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);
}
.rec-plist-per-pic {
  width: 100%;
  height: 65%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #81d4fa;
}
.rec-plist-per-title {
  display: block;
  word-break: normal;
  white-space: pre;
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
  margin: 0.1em;
  font-size: 14px;
}

.rec-plist-per-rating-outer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0.1em;
  font-size: 14px;
}

/* product */
.no-product {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: 3em;
}
.no-product-word {
  margin: 1.8em;
  font-size: 20px;
}
.paging {
  width: 100%;
}
.all-products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.per-product {
  /* width: 220px; */
  margin: 0.5em;
}
.product-cover {
  width: 100%;
  cursor: pointer;
}
.product-beyound-pic {
  margin: 0.3em;
}
.product-per-line {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  align-items: center;
  justify-content: space-between;
}
.product-price {
  /* float: left; */
  display: inline-block;
  color: #ff5252;
  cursor: default;
  font-weight: 600;
}
.product-price::before {
  font-size: 14px;
  content: "￥";
}
.product-title {
  font-size: 14px;
  display: block;
  word-break: normal;
  white-space: pre-line;
  text-overflow: ellipsis;
  overflow: hidden;
  height: 2.4em;
  line-height: 1.2em;
  cursor: pointer;
}

.my-cus-btn-style {
  text-transform: none;
}
</style>