<template>
  <NavBar :showSearch="true" />
  <v-main>
    <div class="no-product" v-if="status !== 1">
      <v-progress-circular
        v-if="status === 0"
        :size="200"
        color="orange"
        indeterminate
      ></v-progress-circular>
      <div v-else-if="status === 2"></div>
      <div v-else-if="status === 3"></div>
      <div v-else-if="status === 4"></div>
      <span class="no-product-word">{{ lWords[status] }}</span>
    </div>
    <v-card
      class="my-product-card"
      v-if="status === 1"
      elevation="8"
      height="80vh"
      width="90vw"
    >
      <div class="cover-div">
        <v-img :src="product.cover"></v-img>
      </div>
      <div class="product-line">
        <v-card-title class="product-title pro-per-line">{{
          product.title
        }}</v-card-title>
        <div class="pro-per-line">
          <v-card-text class="product-store"
            ><v-icon>mdi-store</v-icon>{{ product.store.sname }}</v-card-text
          >
          <div class="product-from">发货地: {{ product.region }}</div>
          <div class="product-ship">邮费: {{ product.ship }}</div>
        </div>
        <!-- ratting & price -->
        <div class="pro-per-line">
          <v-card-text class="product-prices"
            ><span class="money-tag">￥</span
            >{{ product.prices[pselected] }}</v-card-text
          >
          <v-rating
            class="product-rating"
            v-model="product.rating"
            readonly
            size="x-small"
            color="yellow-accent-4"
            half-increments
          ></v-rating>
        </div>
        <!-- detail tab group -->
        <div>
          <div class="tabs-btn-group">
            <v-btn
              v-for="(item, idx) in tabs"
              :key="item.key"
              @click="changeTab(+idx)"
              flat
              class="pl-8 pr-8 my-tabs-btn"
              :color="+tabselected === +idx ? '#e0e5ff' : 'transparent'"
              :text-color="+tabselected === +idx ? 'indigo' : '#888'"
            >
              <v-icon left :icon="item.icon"></v-icon>
              {{ item.name }}
            </v-btn>
          </div>
          <v-card-text>{{ tabs[tabselected].info }}</v-card-text>
        </div>
        <!-- specs -->
        <div class="pro-per-line product-specs">
          <v-btn
            :color="idx === pselected ? 'blue' : '#E3F2FD'"
            :text-color="idx === pselected ? 'white' : '#5C6BC0'"
            v-for="(item, idx) in product.specs"
            :key="idx"
            @click="selectSpec(idx)"
          >
            {{ item }}
          </v-btn>
        </div>
        <v-spacer class="mt-auto"></v-spacer>
        <!-- num, add to card -->
        <div>
          <v-chip class="pa-0" size="large">
            <v-btn
              size="small"
              color="transparent"
              icon="mdi-minus"
              elevation="0"
              class="minus-add-btn"
              @click="changeNum(0)"
            ></v-btn>
            <input
              class="num-ipt"
              type="number"
              v-model="ipt_num"
              onkeypress="return event.charCode >= 48 && event.charCode <= 57"
              @change="changeNum(2)"
              step="1"
            />
            <v-btn
              size="small"
              color="transparent"
              icon="mdi-plus"
              elevation="0"
              class="minus-add-btn"
              @click="changeNum(1)"
            ></v-btn>
          </v-chip>
          <v-card-text>库存: {{ product.stocks[pselected] }}</v-card-text>
        </div>
        <div class="pro-per-line">
          <v-btn color="primary">加入购物车</v-btn>
          <v-btn color="primary">加入愿望单</v-btn>
          <v-btn color="primary">立即购买</v-btn>
        </div>
      </div>
    </v-card>
  </v-main>
</template>

<script>
import NavBar from "../components/NavBar.vue";
import axios from "axios";
import { reactive, ref, onBeforeMount } from "vue";
import { useStore } from "vuex";
export default {
  name: "productPage",
  components: { NavBar },
  setup() {
    let status = ref(0);
    let pselected = ref(0);
    let tabselected = ref(0);
    const store = useStore();
    let product = reactive({});
    let tabs = reactive({});
    let ipt_num = ref(1);
    function getProduct() {
      const pid = new URL(window.location.href).searchParams.get("pid");
      if (pid !== "" && pid !== null && pid !== undefined) {
        axios
          .get(store.getters.UrlProductId(pid))
          .then((res) => {
            if (res.status === 200) {
              if (res.data.code === 200) {
                try {
                  let tmp = res.data.data;
                  Object.assign(tabs, [
                    {
                      key: "description",
                      name: "简介",
                      info: tmp.info,
                      icon: "mdi-text-short",
                    },
                    {
                      key: "coupon",
                      name: "优惠",
                      info: "假设这是一张0元优惠券",
                      icon: "mdi-ticket-percent-outline",
                    },
                    {
                      key: "content",
                      name: "评价",
                      info: "据说是好评",
                      icon: "mdi-comment-text-outline",
                    },
                  ]);
                  Object.assign(
                    product,
                    new Object({
                      title: tmp.pname,
                      cover: store.getters.Media(tmp.cover),
                      rating: 2 + Math.round(Math.random() * 30) / 10,
                      prices: tmp.prices,
                      specs: tmp.specs,
                      stocks: tmp.stocks,
                      ship: "见结算页面",
                      region:
                        tmp.shipping_region === ""
                          ? "全国"
                          : tmp.shipping_region,
                      volume: tmp.volume,
                      store: {
                        sid: tmp.sid,
                        sname: tmp.sname,
                      },
                    })
                  );
                  console.log(product);
                  status.value = 1;
                } catch (e) {
                  console.error(e);
                }
              } else if (res.data.code === 403) {
                status.value = 2;
                alert("no result... try another pid");
              } else {
                status.value = 3;
                alert("something went wrong on the server");
              }
            } else {
              status.value = 4;
              alert("something went wrong on sending data");
            }
          })
          .catch((e) => console.error(e));
      } else {
        window.location.href = "/";
      }
    }

    const lWords = reactive([
      "加载中",
      "",
      "没有找到相关结果，换个id试试",
      "服务器出错",
      "好像网络不太行",
    ]);

    function changeTab(t) {
      tabselected.value = t;
    }

    function selectSpec(t) {
      pselected.value = t;
    }

    function changeNum(t) {
      if (t === 0) {
        ipt_num.value--;
        ipt_num.value = Math.max(1, ipt_num.value);
      } else if (t === 1) {
        ipt_num.value++;
        ipt_num.value = Math.min(
          product.stocks[pselected.value],
          ipt_num.value
        );
      } else if (t === 2) {
        if (ipt_num.value <= 0) ipt_num.value = 1;
        if (ipt_num.value > product.stocks[pselected.value])
          ipt_num.value = product.stocks[pselected.value];
      }
    }

    onBeforeMount(() => {
      getProduct();
    });
    return {
      status,
      pselected,
      product,
      lWords,
      tabselected,
      tabs,
      changeTab,
      ipt_num,
      selectSpec,
      changeNum,
    };
  },
};
</script>

<style scoped>
.no-product {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: 3em;
}
.no-product-word {
  margin: 1.8em;
  font-size: 20px;
}
.my-product-card {
  margin: auto;
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
}
.cover-div {
  width: 50%;
  height: 100%;
  display: inline-flex;
  align-items: center;
}
.product-line {
  width: 50%;
  height: 100%;
  display: flex;
  box-sizing: border-box;
  padding: 1em;
  flex-direction: column;
}
.pro-per-line {
  margin: 1em 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}
.product-line .product-title {
  cursor: default;
}
.product-line .product-store {
  cursor: pointer;
  padding: 0;
}
.product-from,
.product-ship {
  cursor: default;
}
.product-line .product-prices {
  padding: 0;
  font-size: 2em;
  font-weight: bold;
  cursor: default;
  color: #e53311;
}
.product-line .product-prices .money-tag {
  font-size: 0.8em;
}
.tabs-btn-group {
  width: 100%;
  --under-line-width: 5px;
  box-shadow: inset 0px calc(0px - var(--under-line-width)) 0 0px #81818173;
}
.my-tabs-btn {
  border-radius: 0 !important;
  border-bottom: solid var(--under-line-width) indigo;
}
.product-specs {
  justify-content: flex-start;
}
.product-specs > button {
  margin-right: 1em;
}
.num-ipt {
  width: 3em;
  text-align: center;
}
input:focus {
  outline: none;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}
/* .minus-add-btn {
  border-radius: 50%;
} */
</style>